import React from 'react'

const NewsItem=(props)=> {
  
    let { title, description, imageUrl, newsUrl, author, date, source } = props;
    return (

      <div className="my-3">
        <div className="card" style={{ width: "18rem" }}>
          <span className="position-absolute top-0 translate-middle badge rounded-pill bg-danger" style={{ left: '90%', zIndex: '1' }}> {source} </span>
          <img src={imageUrl ? imageUrl : "https://ichef.bbci.co.uk/news/1024/branded_news/12CA5/production/_125856967_p0cl4yjb.jpg"} className="card-img-top" alt="..." />
          <div className="card-body">
            <h5 className="card-title">{title}<span class="badge text-bg-danger mx-3">New</span></h5>
            <p className="card-text">{description}  </p>
            <p className="card-text"><small className="text-muted">By {author} on {new Date(date).toGMTString()} </small></p>
            <a rel="noreferrer" href={newsUrl} target="_blank" className="btn btn-sm btn-dark">Read More</a>
          </div>
        </div>
      </div>
    )
  }


export default NewsItem
